<template>
  <v-col cols="12"
         style="position:relative">
    <!-- <div class="d-flex justify-space-between dvinput"> -->
    <h4 :style="{fontSize: '16px',fontWeight:500,color: labelcolor ? $vuetify.theme.defaults.light.primary : ''}"
        class="pl-2">{{headertext || $t('label_The_recipient')}}</h4>
    <div :style="{'border-bottom':labelcolor? '2px solid '+$vuetify.theme.defaults.light.primary:'1px solid black','background-color':!readonly?'rgb(242, 242, 242)':'#fff'}"
         class="dvinput pl-2">
      <span class="d-flex span1">
        <span class="person"
              v-for="(item,index) in showlist"
              :key='index+item'>
          <v-chip :close='true'
                  :disabled='readonly'
                  @click:close='chipclose(item,index)'>
            {{item}}</v-chip>
        </span>
        <span class="property"
              v-for="(item,index) in showpropertylist"
              :key="index">
          {{item.property}}
        </span>
        <input type="text"
               @input="input"
               @blur="blur"
               @focus="focus"
               :disabled='readonly'
               class="input1"
               :id='inputid'
               v-model="inputvalue">
        <!-- <mrc_ess_input type="text"
                       @input="input"
                       @blur="blur"
                       @focus="focus"
                       :disabled='readonly'
                       class="input1"
                       :id='inputid'
                       v-model="inputvalue"></mrc_ess_input> -->
        <span @click="changeselect"
              v-if="!readonly">
          <v-icon class="material-icons">expand_more</v-icon>
        </span>
      </span>
    </div>
    <!-- ------下拉框 ------->
    <div class="selectbox dvprevent"
         @mousedown.stop='mousedown'
         :class="classshow?'hideorshow':''">
      <div class="d-flex dvprevent dv2"
           v-for="(item,index) in showselectlist"
           :key="index"
           @click="getitemselect($event,item,index)">
        <span class="pl-2 pr-4 dvprevent">
          <v-img src="https://picsum.photos/510/300?random"
                 width="50"
                 height="50"
                 class="dvprevent"></v-img>
        </span>
        <span class="d-flex justify-space-between pr-2 dvprevent"
              style="width:85%">
          <span class="d-flex flex-column dvprevent">
            <span class="dvprevent span4"
                  @mousedown='mousedown'>{{item.christian_name?item.christian_name+' \xa0/\xa0 ':''}}{{item.preferred_name}}</span>
            <span class="dvprevent span3">
              {{item.org_level_uuid}}> {{item.dept_uuid}}> {{item.role_uuids}}
            </span>
          </span>
          <span class="dvprevent">?????</span>
        </span>
      </div>
    </div>
    <!-- 加入变数 -->
    <span class="span2"
          :style="{color: $vuetify.theme.defaults.light.primary}"
          v-if="!readonly"
          @click="addvariate">{{$t('label_Join_the_variables')}}
      <mrc_ess_svgIcon iconClass='open_in_new'></mrc_ess_svgIcon>
    </span>
    <!-- 属性弹出框 -->
    <mrc_ess_myDialog v-model="dialog"
                      :title="$t('label_A_new_level')"
                      width='800'
                      hiddenTitle
                      :disabled='false'
                      @success='save'
                      @cancel='close'>
      <v-card-text>
        <v-tabs v-model="tab"
                grow
                background-color="#ffffff"
                color='primary'
                dark>
          <v-tab v-for="item in items"
                 style="color:black"
                 :key="item.tab">
            {{ item.tab }}
          </v-tab>
        </v-tabs>

        <v-tabs-items v-model="tab">
          <v-tab-item>
            <div v-for="(item,index) in variatelist"
                 :key="index+item">
              <div class="ptbox"
                   @click="changebgd(item,index)"
                   :class="item.divbgd?'divbgd':''">
                <span>{{item.property}}</span>
                <!-- <span style='color:#bcbcbc'>{{item.name}}</span> -->
              </div>
            </div>
          </v-tab-item>

          <v-tab-item>

          </v-tab-item>

        </v-tabs-items>
      </v-card-text>
    </mrc_ess_myDialog>
  </v-col>
</template>

<script>
import { getBusinessList } from '@/service/api/prompt.js'
export default {
  components: {},
  props: {
    function_uuid: {
      type: String
    },
    updateFunction_uuid: {
      type: String
    },
    selectlist: { //所有下拉框列表
      type: Array
    },
    showlist: { //用户选择的主收件人
      type: Array
    },
    propertylist: { // 用户选择的字段属性
      type: Array
    },
    inputid: {
      type: String,
      default: 'pycredit_name'
    },
    headertext: {
      type: String
    },
    readonly: {
      type: Boolean,
      default: false
    },
    iconid: {
      type: String,
      default: 'meass'
    }
  },
  data () {
    return {
      labelcolor: false,
      classshow: true,
      inputvalue: '',
      dialog: false,
      tab: null,
      items: [
        { tab: '功能变量' },
        { tab: '系统变量' },
      ],
      variatelist: [],
      showpropertylist: [],//
      showselectlist: [],//显示的下拉框
    };
  },
  computed: {},
  created () { },
  watch: {
    propertylist: {
      handler (v) {
        this.showpropertylist = v
      }, deep: true
    }
  },
  mounted () {
    this.showselectlist = this.selectlist      //
    this.showpropertylist = this.propertylist
  },

  methods: {
    mousedown (event) {
      event.preventDefault()
    },
    replaceitem (arr, arr2) {
      arr2.map(item => {
        const property = item.property
        var is = true
        for (let i = 0; i < arr.length; i++) {
          if (property == arr[i].property) {
            is = false
            break
          }
        }
        if (!is) {
          item.divbgd = true
        }
        else {
          item.divbgd = false
        }
      })
    },
    getitemselect (e, item, index) {//点击获取下拉属性
      document.getElementById(this.inputid).focus();
      if (this.showlist.includes(item.staff_email)) {
        this.$store.commit('newsTips', this.$t('message_The_recipient_has_been_added'))
      }
      else if (this.showlist.includes(item.tricode)) {
        this.$store.commit('newsTips', this.$t('message_The_recipient_has_been_added'))
      }
      else {
        this.inputvalue = item.staff_email || item.tricode
      }
    },
    async getvariatelist () { //获取属性字段列表 
      //发送请求)
      if (!this.variatelist.length > 0) {
        let res = null
        if (this.$store.state.uuid) {
          res = await getBusinessList({
            prompt_name: 'email_address_field',
            function_uuid: this.updateFunction_uuid
          })
        }
        else {
          res = await getBusinessList({
            prompt_name: 'email_address_field',
            function_uuid: this.function_uuid
          })
        }
        this.variatelist = res.data.map(item => {
          item.property = '{{' + item.field_name + '}}'
          delete item.field_name
          return item
        })
      }
      // this.variatelist = [ //功能变量
      //   {
      //     property: '{{saaas}}',
      //     name: 'xasxaxaaxa'
      //   },
      //   {
      //     property: '{{xasaokhhh}}',
      //     name: 'xasxaxaaxa'
      //   },
      //   {
      //     property: '{{qqqws}}',
      //     name: 'xasxaxaxaxaaxa'
      //   },
      //   {
      //     property: '{{qwertyui}}',
      //     name: 'xaxxsxaxxaaxa'
      //   }
      // ]
      this.replaceitem(this.showpropertylist, this.variatelist)
    },
    changeselect () {
      this.classshow = !this.classshow
      this.showselectlist = this.selectlist
    },
    chipclose (item, index) {
      this.showlist.splice(index, 1)
    },
    blur (v) { //失去焦点
      this.labelcolor = !this.labelcolor
      this.classshow = true
      if (this.inputvalue && !this.showlist.includes(this.inputvalue)) {
        this.showlist.push(this.inputvalue)
        this.inputvalue = ''
      }
      else {
        if (!this.inputvalue) {
        }
        else {
          this.$store.commit('newsTips', this.$t('message_Do_not_add_repeatedly'))
          this.inputvalue = ''
        }
      }
    },
    input () {
      var showselectlist = []
      if (this.inputvalue) {
        this.selectlist.map(item => {
          if (item.preferred_name.indexOf(this.inputvalue) != -1) {
            showselectlist.push(item)
          }
          if (item.staff_email.indexOf(this.inputvalue) != -1) {
            showselectlist.push(item)
          }
          if (item.tricode.indexOf(this.inputvalue) != -1) {
            showselectlist.push(item)
          }
        })
      }
      this.showselectlist = showselectlist
      this.classshow = false
    },
    focus () {
      this.labelcolor = !this.labelcolor
    },
    async addvariate () {
      this.dialog = true
      await this.getvariatelist()
    },
    changebgd (item, index) {
      item.divbgd = !item.divbgd
      this.$set(this.variatelist, index, item)
    },
    close () {
      this.dialog = false
      let newpropertylist = []
      this.variatelist.map(item => {
        if (item.divbgd == true) {
          newpropertylist.push(item)
        }
      })
      this.showpropertylist = newpropertylist
      // console.log(this.showpropertylist, this.showpropertylist.length);
      this.$emit('getpropertylist', this.showpropertylist)
    },
    save () {

    },
  },
};
</script>

<style scoped>
.bcb {
  border-bottom: 2px solid #0f46c1;
}
.hideorshow {
  display: none;
}
.selectbox {
  position: absolute;
  top: 66px;
  z-index: 1;
  background: #ffffff;
  width: 600px;
  max-height: 400px;
  overflow-y: auto;
}
.dv2 {
  padding: 10px 0 0;
}
.dvinput {
  height: 40px;
  line-height: 40px;
}
.ptbox {
  display: flex;
  flex-direction: column;
  height: 50px;
  border-bottom: 2px solid #eeeef0;
}
.divbgd {
  background-color: #fff7e6;
}
.input1 {
  height: 40px;
  line-height: 40px;
  border: 0;
  outline: none;
  background-color: rgba(0, 0, 0, 0);
  flex-grow: 1;
}
.property {
  font-size: 22px;
}
.span1 {
  width: 100%;
  display: flex;
}
.span2 {
  font-size: 14px;
  font-weight: 550;
}
.span2 /deep/.svg-icon {
  font-size: 25px;
}
.span3 {
  width: 350px;
  overflow: hidden; /*超出部分隐藏*/
  white-space: nowrap; /*不换行*/
  text-overflow: ellipsis; /*超出部分省略号显示*/
}
.span4 {
  color: black;
}
</style>
